/*               */
/* css variables */
/*               */
* {
    --cardZindex: 4;
    
    --transparentBackground: rgba(255, 255, 255, 0);
    --translucentBackground: rgba(211, 211, 211, .25);

    --boxShadowSettings: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* format card parent */
.cardContainer {

    /* overlay */
    z-index: var(--cardZindex);
    position: absolute; /* super important */

    /* coordinate */
    top: 20%; /* top of element x% down from top of screen! */
    left: 15%;
    margin-right: -50%;

    /* size */
    width: 70vw;
    height: auto; /* auto-sized to sized of contents */

    /* text formatting */
    display: block;
    text-align: right;
    
    padding: 20px;
    background-color: white;
    border-radius: 1rem;
    box-shadow: var(--boxShadowSettings);
}

/* format card children (contents) */
.textBlock {
    /* display controls whether each item
       item is on the same line or stacked */
    display: block;
    color: black;
}

#closeBtn {
    float: right;
    width: auto;
    position: absolute;
    display: block;
}
body {
    font-family: "Roboto", sans-serif;
}

/*               */
/* css variables */
/*               */
* {
    --sidebarZindex: 5;
    
    --transparentBackground: rgba(255, 255, 255, 0);
    --translucentBackground: rgba(211, 211, 211, .25);
}

/*                                     */
/* main container + open button + logo */
/*                                     */

/* parent tag */
#main {
    position: absolute;
    width: 100vw;
    z-index: var(--sidebarZindex);

    transition: margin-left .5s;
    padding: 16px;
}

.openbtn {
    position: absolute;

    font-size: 25px;
    cursor: pointer;
    background-color: var(--transparentBackground);
    color: black;
    border: none;
    border-radius: 2rem;
    
}
.openbtn:hover {
    background-color: var(--translucentBackground);
    border-radius: 2rem;
}

.logo{
    position: relative;
    float: right;

    background-color: rgb(0, 255, 191);
    
    font-family: "Karla", sans-serif;
    font-size: larger;
    font-weight: bolder;
    color: black;
}

.logo:hover {
    color: lightgray;
    text-decoration: none;
}


/*                        */
/* macro sidebar settings */
/*                        */

/* parent tag */
.sidebar {
    position: absolute;
    height: 25vh;
    width: 0;
    top: 0;
    left: 0;
    z-index: var(--sidebarZindex);
    
    background-color:rgba(255, 255, 255, 0);
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
}

.sidebar a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 15px;
    color: black;
    display: block;
    transition: 0.3s;
}
.sidebar a:hover {
    color: lightgray;
}

.sidebar .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 20px;
    margin-left: 50px;
}

#aTag {
    /* background-color: white; */
}


/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
    .sidebar {padding-top: 15px;}
    .sidebar a {font-size: 18px;}
}
